import React from 'react';
import { Col, Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import axios from 'axios'
export default class Login extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      name: "",
      pass: "",
      account: []
    }
  }

  componentDidMount() {
    axios.get('http://localhost:5001/api/account/')
      .then(res => {
        const account = res.data;
        this.setState({
          name: "",
          pass: "",

          account: account
        })
      })
  }

  onNameChange = e => {
    this.setState({
      name: e.target.value,
      pass: this.state.pass
    })
  }

  onPassChange = e => {
    this.setState({
      name: this.state.name,
      pass: e.target.value
    })
  }

  render() {

    let {onLogIn} = this.props
    let { name,pass,account} = this.state

    return (
      <div>
        <div className="column is-full" align="center">
          <div className="box box-primary"style={{backgroundColor: '#e6f2ff', width:'1000px'}}>
            <div>
              <h3  align="center">
                Log in
              </h3>
            </div>

            <br/>

            <div>
              <div>
                <Form >
                    <FormGroup>
                        <Label for="exampleUsername">Username</Label>
                        <Col sm={6}>
                            <Input type="text" onChange={this.onNameChange} />
                        </Col>
                    </FormGroup>
                    <FormGroup>
                        <Label for="examplePassword">Password</Label>
                        <Col sm={6}>
                            <Input type="password" onChange={this.onPassChange}/>
                        </Col>
                    </FormGroup>
                    <Button onClick={() => onLogIn(name,pass,account)} color="danger">Submit</Button>
                </Form>
              </div>
              <br/>
            </div>
          </div>
        </div>
      </div>
    )
  }
}