import React from 'react';
import { Col, Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import axios from 'axios'
import {Link} from 'react-router-dom'

export default class SignIn extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      name: "",
      pass: "",
      gender: "Male",
      weight: "",
      height: "",
      age: "",
      account: []
    }
  }

  componentDidMount() {
    axios.get('http://localhost:5001/api/account/')
      .then(res => {
        const account = res.data;
        this.setState({
          name: "",
          pass: "",
          gender: "Male",
          weight: "",
          height: "",
          age: "",
          account: account
        })
      }).then(() => console.log(this.state))
  }

  onNameChange = e => {
    this.setState({
      name: e.target.value,
      pass: this.state.pass,
      gender: this.state.gender,
      weight: this.state.weight,
      height: this.state.height,
      age: this.state.age
    })
  }

  onPassChange = e => {
    this.setState({
      name: this.state.name,
      pass: e.target.value,
      gender: this.state.gender,
      weight: this.state.weight,
      height: this.state.height,
      age: this.state.age
    })
  }

  onGenderChange = e => {
    this.setState({
      name: this.state.name,
      pass: this.state.pass,
      gender: e.target.value,
      weight: this.state.weight,
      height: this.state.height,
      age: this.state.age
    })
  }

  onWeightChange = e => {
    this.setState({
      name: this.state.name,
      pass: this.state.pass,
      gender: this.state.gender,
      weight: e.target.value,
      height: this.state.height,
      age: this.state.age
    })
  }

  onHeightChange = e => {
    this.setState({
      name: this.state.name,
      pass: this.state.pass,
      gender: this.state.gender,
      weight: this.state.weight,
      height: e.target.value,
      age: this.state.age
    })
  }

  onAgeChange = e => {
    this.setState({
      name: this.state.name,
      pass: this.state.pass,
      gender: this.state.gender,
      weight: this.state.weight,
      height: this.state.height,
      age: e.target.value
    })
  }

  render() {

    let { onSignIn } = this.props
    let { name,pass,gender,weight,height,age,account } = this.state
    
    return (
      <section align="left">
          <div>
            <div className="box box-primary" style={{backgroundColor: '#e6f2ff'}}>
              <div>
                <h3  align="center">
                  Sign in
                </h3>
              </div>
              <br/>
            <div>
            <Form >
              <FormGroup>
                <Label for="exampleUsername">Username</Label>
                <Input type="text" placeholder="Username" onChange={this.onNameChange}/>
              </FormGroup>
              <FormGroup>
                <Label for="examplePassword">Password</Label>
                <Input type="password" placeholder="Password" onChange={this.onPassChange}/>
              </FormGroup>
              <FormGroup tag="fieldset">
                <label>Gender</label>
                <Input type="select" onChange={this.onGenderChange}>
                  <option>Male</option>
                  <option>Female</option>
                </Input>
              </FormGroup>
              <FormGroup>
                <Label for="exampleWeight">Weight</Label>
                <Input type="number" placeholder="(kg)" onChange={this.onWeightChange}/>
              </FormGroup>
              <FormGroup>
                <Label for="exampleHeight">Height</Label>
                <Input type="number" placeholder="(cm)" onChange={this.onHeightChange}/>
              </FormGroup>
              <FormGroup>
                <Label for="exampleAge">Age</Label>
                <Input type="number" placeholder="(years)" onChange={this.onAgeChange}/>
              </FormGroup>
                <Button type="button" color="danger" onClick={() => onSignIn(name,pass,gender,weight,height,age,account)}>Submit</Button>
              </Form>
              <br/>
              </div>
            </div>
          </div>
      </section>
    );
  }
}
