import React, {Component} from 'react'
import axios from 'axios'
import { Progress } from 'reactstrap'
export default class CalMiter extends Component {

    constructor(props) {
        super(props)
        this.state = {
            account: [],
            food: []
        }
    }

    render() {

        let {user} = this.props

        axios.get('http://localhost:5001/api/account/')
        .then(res => {
            const account = res.data;
            this.setState({
                account: account
            })
        })

        axios.get('http://localhost:5000/api/food/')
        .then(res => {
            const food = res.data;
            this.setState({
                food: food
            })
        })

        let weight = 0
        let height = 0
        let age = 0
        let gender = ""

        this.state.account.some(e => {
            if (e.name === user && e.s !== "save") {
                weight = e.weight
                height = e.height
                age = e.age
                gender = e.gender
            }
        })

        let bms = 0;

        if (gender == "Male") {
            bms = 66 + (13.7 * weight) + (5 * height) - (6.8 * age)
        } else {
            bms = 665 + (9.6 * weight) + (1.8 * height) - (4.7 * age)
        }

        bms = bms * 1.2
        bms = Math.floor(bms)

        let now = new Date()

        let timer = now.getDate() + "/" + now.getMonth() + "/" + now.getFullYear()

        let currentCal = 0;

        for (let f of this.state.food) {
            if (f.time == timer && f.account == user) {
                currentCal += f.cal
            }
        }

        let val = (currentCal / bms) * 100

        let txt = "warning"
        if (val >= 100) {txt = "danger"}
        return(
            <div style={{backgroundColor: '#e6f2ff'}}>
                <div className="text-center">Daily Callories</div>
                <Progress animated color={txt} value={val}>{currentCal} of {bms} Kcal</Progress>
                <br/>
            </div>
        )
    }
}