import React, {Component} from 'react'
import CalForm from './CalForm'
import CalTable from './CalTable'
import axios from 'axios'
import CalMiter from './CalMiter'

export default class CalMain extends React.Component {

    render() {
        let {user} = this.props
        return(
            <div>
                <CalForm user={user}/>
                <CalTable user={user}/>
            </div>
        )
    }
}
