import React, {Component} from "react"
import { Button } from "reactstrap"
import Axios from "axios"

class CalItem extends Component {

    constructor(props){
        super(props)
        this.state = {
            id: this.props.data._id
        }
    }

    onDelete = e => {
        Axios.delete(`http://localhost:5000/api/food/${this.state.id}`)
        window.location.reload(false)
    }

    render() {

        let {user, data} = this.props
        if (data.account == user){
            return(
                <tr >
                    <td >{data.time}</td>
                    <td>{data.meal}</td>
                    <td>{data.name}</td>
                    <td>{data.cal}</td>
                    <td align="right"><Button type="submit" onClick={this.onDelete}>Delete</Button></td>
                </tr>
            )
        } else {
            return(<div></div>)
        }
    }
}

export default CalItem