import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'
import './App.css';
import 'bulma/css/bulma.css'
import axios from 'axios'
import MainPage from './pages/mainPage'
import LoginInPage from './pages/logInPage'
import SignInPage from './pages/signInPage'
import First from './components/First'
import WorkerPage from './pages/workerPage'
import ProfilePage from './pages/profilePage'
import OrganPage from './pages/organPage'

class App extends Component {


  constructor(props){
    super(props)
    this.state = {
      account: []
    }

    this.onSignIn = this.onSignIn.bind(this)
    this.onLogIn = this.onLogIn.bind(this)
    this.onLogOut = this.onLogOut.bind(this)
  }

  onSignIn(name,pass,gender,weight,height,age,account) {

    let id = ""

    account.some(e => {
      if (e.s === "save") {
        id = e._id
      }
    })

    if (id === ""){
      
    } else {
      axios.delete(`http://localhost:5001/api/account/${id}`)
    }

    if (name === "" || pass === "" || weight <= 0 || height <= 0 || age <= 0) {
      alert("Incorrect Input")
    } else if (account.some(e => e.name === name)) {
      alert("Username is not allow")
    } else {
      axios.post(`http://localhost:5001/api/account`, {name,pass,weight,height,gender,age})

      let s = "save"
      axios.post(`http://localhost:5001/api/account`, {s,name})

      window.location.href = ("/main")
    }
  }

  onLogIn(name,pass,account) {

    let id = ""

    account.some(e => {
      if (e.s === "save") {
        id = e._id
      }
    })

    if (id === ""){
      
    } else {
      axios.delete(`http://localhost:5001/api/account/${id}`)
    }

    if (account.some(e => e.name === name && e.pass === pass)) {
      let s = "save"
      axios.post(`http://localhost:5001/api/account`, {s,name})

      window.location.href = ("/main")
    } else {
      alert("Incorrect Username or Password")
      window.location.reload(false)
    }
  }

  onLogOut() {
    window.location.href = ("/")
  }

  onOrgan(name,pass,gender,weight,height,age,account) {
    let id = ""
    axios.get()
    account.some(e => {
      if (e.name === name && e.s != "save") {
        id = e._id
      }
    })

    if (id === ""){

    } else {
      axios.delete(`http://localhost:5001/api/account/${id}`)
    }

    if (name === "" || pass === "" || weight <= 0 || height <= 0 || age <= 0) {
      alert("Incorrect Input")
    } else {
      axios.post(`http://localhost:5001/api/account`, {name,pass,weight,height,gender,age})

      window.location.href = ("/main")
    }
    
  }

  render() {
    return (
      <Router>
        <div className="App">
          <div className="container">
          <Switch>
              <Route exact path='/' component={ First }/>
              <Route path='/signin' component={() => <SignInPage onSignIn={this.onSignIn}/>}/>
              <Route path='/main' component={() => <MainPage onLogOut={this.onLogOut}/>}/>
              <Route path='/login' component={() => <LoginInPage onLogIn={this.onLogIn}/>}/>
              <Route path='/organ' component={() => <OrganPage onOrgan={this.onOrgan}/>}/>
              <Route path='/profile' component={() => <ProfilePage/>}/>
              <Route path='/worker' component={() => <WorkerPage />}/>
            </Switch>
          </div>
        </div>
      </Router>
    );
  }
}

export default App


