import React from 'react';
import { Jumbotron, Button } from 'reactstrap';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'


const Example = (props) => {
  return (
    <section>
      <Jumbotron style={{backgroundColor: 'white'}}>
        <img src={"https://firebasestorage.googleapis.com/v0/b/lab8-d9b0d.appspot.com/o/Final-project.jpg?alt=media&token=ed2c56f3-da1e-4c00-b239-601e0b32c790" } className='card-img-top' alt="..." />
        <br/>
        <br/>
        <div class='row'>
            <div className='col-md-6'>
                <h1 className="lead1">
                    <Link to={'signin'}>
                      <Button className="float-right" outline color="danger">Sign in</Button>
                    </Link>
                </h1>
            </div>
            <div className='col-md-6' style={{padding: '.5px'}}>
                <h1>
                  <Link to={'/login'}>
                    <Button className="float-left" outline color="danger">Log in</Button>
                  </Link>
                </h1>
            </div>
        </div>
      </Jumbotron>
    </section>
  );
};

export default Example;