import React, { useState } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText
} from 'reactstrap';

export default class Header extends React.Component {


  constructor(props){
    super(props)
    this.state = {
      isOpen: false
    }
  }

  toggle = () => {
    let {isOpen} = this.state
    this.setState({
      isOpen: !isOpen
    })
  }

  render(){

    let {isOpen} = this.state
    let {user} = this.props
    
    console.log(user)
    return (
      <div>
        <Navbar style={{backgroundColor: 'salmon'}} light expand="lg">
          <NavbarBrand href="/">
            <h1 style={{color:"lightcyan", fontFamily:"kanitBlackItalic"}}>
              Calleries Diary
            </h1>
          </NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={isOpen} navbar className="justify-content-end">
            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret>
                <p style={{color:"lightcyan",fontSize:18}}>
                  Signed in as: {user}
                </p>
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem onClick={() => {window.location.href = ("/main")}}>
                  Main
                </DropdownItem>
                <DropdownItem onClick={() => {window.location.href = ("/profile")}}>
                  Profile
                </DropdownItem>
                <DropdownItem onClick={() => {window.location.href = ("/organ")}}>
                  Organizer
                </DropdownItem>
                <DropdownItem onClick={() => {window.location.href = ("/worker")}}>
                  Info
                </DropdownItem>
                <DropdownItem divider />
                <DropdownItem onClick={() => this.props.onLogOut()}>
                  Log out
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}
