import React from 'react'
import axios from 'axios'
import { Col, Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import Header from '../components/Header'
export default class profilePage extends React.Component{
    
    constructor(props){
        super(props)
        this.state = {
            account: []
        }
    }

    componentDidMount(){
        axios.get(`http://localhost:5001/api/account`)
        .then(res => {
          const account = res.data
          this.setState({account})
        })
    }
    
    render() {

        let name = ""

        this.state.account.some(e => {
          if (e.s === "save") {
            name = e.name
          }
        })

        let gender = ""
        let weight = ""
        let height = ""
        let age = ""

        this.state.account.some(e => {
            if (e.s != "save" && e.name === name) {
                gender = e.gender
                weight = e.weight
                height = e.height
                age = e.age
            }
        })

        return(
            <div>
                <Header user={name}/>
                <section align="left">
                    <div>
                        <div className="box box-primary" style={{backgroundColor: '#e6f2ff'}}>
                            <div>
                                <h3  align="center">
                                    Profile
                                </h3>
                            </div>
                            
                            <br/>
                            
                            <div>
                                <Form >
                                    <FormGroup>
                                        <Label for="exampleUsername">Username : {name}</Label>
                                    </FormGroup>
                                    <FormGroup tag="fieldset">
                                        <Label>Gender : {gender}</Label>
                                    </FormGroup>
                                    <FormGroup>
                                        <Label for="exampleWeight">Weight : {weight}</Label>
                                    </FormGroup>
                                    <FormGroup>
                                        <Label for="exampleHeight">Height : {height}</Label>
                                    </FormGroup>
                                    <FormGroup>
                                        <Label for="exampleAge">Age : {age}</Label>
                                    </FormGroup>
                                </Form>
                                <br/>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}