import React, {Component} from 'react';
import { Col, Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import CalTable from './CalTable'
import axios from 'axios'
import { queryByAttribute } from '@testing-library/react';
import CalMiter from './CalMiter';

class CalForm extends Component {

  constructor(props){
    super(props)
    
    this.state = {
      account: "",
      time: "",
      meal: "Breakfast",
      name: "",
      cal: ""
    }
  }

  onMealChange = e => {
    this.setState({
      account: this.state.account,
      time: "",
      meal: e.target.value,
      name: this.state.name,
      cal: this.state.cal
    })
    console.log(e.target.value)
  }

  onNameChange = e => {
    this.setState({
      account: this.state.account,
      time: "",
      meal: this.state.meal,
      name: e.target.value,
      cal: this.state.cal
    })
    console.log(e.target.value)
  }

  onCalChange = e => {
    this.setState({
      account: this.state.account,
      time: "",
      meal: this.state.meal,
      name: this.state.name,
      cal: e.target.value
    })
    console.log(e.target.value)
  }

  onSubmit = e => {

    let {user, changeUser} = this.props

    if (this.state.name == "" || this.state.cal <= 0){
      alert("Wrong Format Please Input Again!!")
    } else {
      let now = new Date()

      const account = user
      const time = now.getDate() + "/" + now.getMonth() + "/" + now.getFullYear()
      const meal = this.state.meal
      const name = this.state.name
      const cal = this.state.cal

      axios.post(`http://localhost:5000/api/food/`, { account,time,meal,name,cal })

    } 
    
  }

  render() {
    return (
      <div>
      <section align="left">
          <div>
            <div className="box box-primary" style={{backgroundColor: '#e6f2ff'}}>
              <div>
                <h3  align="center">
                  Callories Diary
                </h3>
              </div>

              <br/>

              <div>
                <Form >

                  <FormGroup>
                      <Label for="exampleSelect">Meal</Label>
                      <Input type="select" name="select" id="exampleSelect" onChange={this.onMealChange}>
                          <option>Breakfast</option>
                          <option>Lunch</option>
                          <option>Dinner</option>
                          <option>Other</option>
                      </Input>
                  </FormGroup>
                  
                  <FormGroup>
                    <Label for="exampleFood">Food</Label>
                    <Input type="text" name="food" id="exampleFood" onChange={this.onNameChange}/>
                  </FormGroup>
                  
                  <FormGroup>
                    <Label for="exampleCalleries">Calleries (Kcal)</Label>
                    <Input type="number" name="Calleries" id="exampleCalleries" onChange={this.onCalChange}/>
                  </FormGroup>
                  
                  <Button type="submit" color="danger" onClick={this.onSubmit}>Submit</Button>
                </Form>
                
                <br/>
              
              </div>
            </div>
          </div>
      </section>
      <CalMiter user = {this.props.user}/>
      </div>
    )
  }
}

export default CalForm