import React, { Component } from 'react';
import { Table } from 'reactstrap';
import CalItem from './CalItem'
import axios from 'axios'

class CalTable extends Component {

  state = {
    food: []
  }

  componentDidMount() {
    axios.get('http://localhost:5000/api/food/')
      .then(res => {
        const food = res.data;
        this.setState({food});
      })
  }

  render() {
    let {user} = this.props
    console.log(user)
    return (
      <div>
        <Table>
          <thead style={{backgroundColor: 'salmon'}}>
            <tr>
              <th width="15%">D/M/Y</th>
              <th width="20%">Meal</th>
              <th width="40%">Food List</th>
              <th width="15%">Calories</th>
              <th width="10%" align="center"></th>
            </tr>
          </thead>
          <tbody style={{backgroundColor: 'lightcyan'}}>
            {
              this.state.food.map(data => <CalItem data={data} user={user}/>)
            }
          </tbody>
        </Table>
      </div>
    );
  }
}

export default CalTable;